// This is for Progress bar animation also has a js code
@keyframes animate-positive {
	0% {
		width: 0;
	}
}
.spin {
	animation: spin 15s linear infinite;
}
@keyframes spin {
	0% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
	}
}
/*img-animation**********************/
.img-custom-anim-right {
	animation: img-anim-right 0.9s forwards cubic-bezier(0.645, 0.045, 0.355, 1) 0.4s;
	opacity: 0;
}
@keyframes img-anim-right {
	0% {
		transform: translateX(5%);
		clip-path: inset(0 0 0 100%);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		clip-path: inset(0 0 0 0);
		opacity: 1;
	}
}
.img-custom-anim-left {
	animation: img-anim-left 0.9s forwards cubic-bezier(0.645, 0.045, 0.355, 1) 0.4s;
	opacity: 0;
}
@keyframes img-anim-left {
	0% {
		transform: translateX(-5%);
		clip-path: inset(0 100% 0 0);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		clip-path: inset(0 0 0 0);
		opacity: 1;
	}
}
.img-custom-anim-top {
	animation: img-anim-top 0.9s forwards cubic-bezier(0.645, 0.045, 0.355, 1);
	opacity: 0;
}
@keyframes img-anim-top {
	0% {
		transform: translateY(-5%);
		clip-path: inset(0 0 100% 0);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		clip-path: inset(0 0 0 0);
		opacity: 1;
	}
}
@media (max-width: 767px) {
	.img-custom-anim-right,
	.img-custom-anim-left,
	.img-custom-anim-top {
		animation: none;
		opacity: 1;
	}
}
.slideinleft,
.slideinright,
.slideindown,
.slideinup {
	opacity: 0;
	animation-fill-mode: forwards;
	animation-iteration-count: 1;
	animation-duration: 0.9s;
	animation-delay: 0.3s;
	animation-name: var(--animation-name);
}
.swiper-slide-active {
	.slideinleft {
		--animation-name: slideinleft;
	}
	.slideinright {
		--animation-name: slideinright;
	}
	.slideinup {
		--animation-name: slideinup;
	}
	.slideindown {
		--animation-name: slideindown;
	}
}
@keyframes slideinleft {
	0% {
		opacity: 0;
		transform: translateX(-100px);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}
@keyframes slideinright {
	0% {
		opacity: 0;
		transform: translateX(180px);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}
@keyframes slideindown {
	0% {
		opacity: 0;
		transform: translateY(-100px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}
@keyframes slideinup {
	0% {
		opacity: 0;
		transform: translateY(100px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

/**
  * Rays
  *
  * @author jh3y
*/
@-webkit-keyframes rays {
	from {
		opacity: 1;
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	to {
		opacity: 0;
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}
@keyframes rays {
	from {
		opacity: 1;
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	to {
		opacity: 0;
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}

.rays:after,
.rays:before {
	border-radius: 100%;
	content: "";
	position: absolute;
}

.rays:after {
	-webkit-animation: rays 1.5s infinite;
	animation: rays 1.5s infinite;
	border: 10px solid var(--tc-theme-primary);
	height: 100px;
	width: 100px;
}

.rays:before {
	background: var(--tc-theme-primary);
	height: 20px;
	width: 20px;
}

.effect-curtain {
	position: relative;
	overflow: hidden;
	.firstImg {
		transition: transform 0.9s;
	}
	&:hover {
		.firstImg {
			transform: scale(1.2);
		}
		.secondImg {
			transform: translateX(-700px) skewX(40deg);
		}
		.thirdImg {
			transform: translateX(700px) skewX(-40deg);
		}
	}
	.secondImg {
		position: absolute;
		top: 0;
		z-index: 3;
		transition: transform 1.6s;
		left: 0;
		clip-path: polygon(0 0, 50% 0, 50% 100%, 0% 100%);
	}
	.thirdImg {
		position: absolute;
		top: 0;
		z-index: 3;
		transition: transform 1.6s;
		right: 0;
		clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
	}
}
/* Copy code to element you want to animate */
.shake-vertical {
	animation: shakeVertical 10s ease 0s infinite normal none;
}

@keyframes shakeVertical {
	0%,
	100% {
		transform: translateY(0);
	}

	10%,
	30%,
	50%,
	70% {
		transform: translateY(-8px);
	}

	20%,
	40%,
	60% {
		transform: translateY(8px);
	}

	80% {
		transform: translateY(6.4px);
	}

	90% {
		transform: translateY(-6.4px);
	}
}
.hover-rotate-1 {
	transition: 0.3s;
	&:hover {
		transform: rotate(45deg);
		transition: 0.3s;
	}
}
