@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Manrope:wght@200..800&display=swap");

// Font Family
$theme-fonts: (
	body: (
		font-family: '"DM Sans", sans-serif',
	),
	heading: (
		font-family: '"DM Sans", sans-serif',
	),
	display: (
		font-family: '"DM Sans", sans-serif',
	),
);
// Font Size
$font-size: (
	body: (
		font-size: 16px,
	),
	fs: (
		1: 40px,
		2: 32px,
		3: 30px,
		4: 24px,
		5: 18px,
		6: 16px,
		7: 14px,
		8: 12px,
	),
	ds: (
		1: 80px,
		2: 72px,
		3: 64px,
		4: 56px,
		5: 48px,
		6: 40px,
	),
	ds-xs: (
		1: 64px,
		2: 56px,
		3: 48px,
		4: 36px,
		5: 28px,
		6: 20px,
	),
);
// Line Height
$theme-lh: (
	body: (
		line-height: 1.5,
	),
	heading: (
		line-height: 1.2,
	),
);
// Font Weight
$font-weight: (
	fw: (
		extra-bold: 800,
		bold: 700,
		semi-bold: 600,
		medium: 500,
		regular: 400,
		light: 300,
	),
);
/*=============================
	Typography css start
===============================*/
