	top: 100px;
}
.bg-linear-1 {
	background: linear-gradient(180deg, rgba(249, 246, 241, 0.37) 0%, rgba(147, 145, 142, 0) 100%);
}
.hover-up {
	transition: 0.3s ease-in-out;
	&:hover {
		transform: translateY(-3px);
		transition: 0.3s ease-in-out;
	}
}
.fs-18px {
	font-size: 18px;
}
img,
.img {
	max-width: 100%;
	transition: 0.3s;
}
a,
button {
	color: var(--tc-neutral-1000);
	outline: none;
	text-decoration: none;
	transition: 0.3s;
}
a:focus,
.btn:focus,
.button:focus {
	text-decoration: none;
	outline: none;
	box-shadow: none;
}
a:hover {
	color: var(--tc-theme-primary) !important;
	text-decoration: none;
}
.btn:hover {
	color: var(--bs-btn-hover-color) !important;
	background-color: var(--bs-btn-hover-bg);
	border-color: var(--bs-btn-hover-border-color);
}
button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
	outline: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: var(--tc-heading-font-family);
	color: var(--tc-heading-color);
	margin-top: 0px;
	font-weight: var(--tc-fw-semi-bold);
	line-height: var(--tc-heading-line-height);
	text-transform: unset;
	letter-spacing: 0.5px;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
	color: inherit;
}
h1 {
	font-size: 60px;
}
h2 {
	font-size: 48px;
}
h3 {
	font-size: 38px;
}
h4 {
	font-size: 31px;
}
h5 {
	font-size: 25px;
}
h6 {
	font-size: 20px;
}
.list-wrap {
	margin: 0px;
	padding: 0px;
	& li {
		list-style: none;
	}
}
p {
	font-family: var(--tc-body-font-family);
	font-size: var(--tc-body-font-size);
	line-height: var(--tc-body-line-height);
	font-weight: var(--tc-fw-regular);
	color: var(--tc-body-color);
	margin-bottom: 15px;
}
hr {
	border-bottom: 1px solid var(--tc-color-gray-1);
	border-top: 0 none;
	margin: 30px 0;
	padding: 0;
}
label {
	color: var(--tc-heading-color);
	cursor: pointer;
	font-size: var(--tc-body-font-size);
	font-weight: var(--tc-fw-regular);
}
input[type="color"] {
	appearance: none;
	-moz-appearance: none;
	-webkit-appearance: none;
	background: none;
	border: 0;
	cursor: pointer;
	height: 100%;
	width: 100%;
	padding: 0;
	border-radius: 50%;
}
*::-moz-selection {
	background: var(--tc-theme-primary);
	color: var(--tc-neutral-0);
	text-shadow: none;
}
::-moz-selection {
	background: var(--tc-theme-primary);
	color: var(--tc-neutral-0);
	text-shadow: none;
}
::selection {
	background: var(--tc-theme-primary);
	color: var(--tc-neutral-0);
	text-shadow: none;
}
/*=============================
    - Input Placeholder
===============================*/
input,
textarea {
	color: var(--tc-body-color);
}
*::-moz-placeholder {
	color: var(--tc-body-color);
	font-size: var(--tc-body-font-size);
	opacity: 1;
}
*::placeholder {
	color: var(--tc-body-color);
	font-size: var(--tc-body-font-size);
	opacity: 1;
}
/*=============================
    - Common Classes
===============================*/
.fix {
	overflow: hidden;
}
.clear {
	clear: both;
}
